/* overpass-100normal - latin */
@font-face {
  font-family: 'Overpass';
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src:
    local('Overpass Thin '),
    local('Overpass-Thin'),
    url('./files/overpass-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/overpass-latin-100.woff') format('woff'); /* Modern Browsers */
}

/* overpass-100italic - latin */
@font-face {
  font-family: 'Overpass';
  font-style: italic;
  font-display: swap;
  font-weight: 100;
  src:
    local('Overpass Thin italic'),
    local('Overpass-Thinitalic'),
    url('./files/overpass-latin-100italic.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/overpass-latin-100italic.woff') format('woff'); /* Modern Browsers */
}

